<template>
  <section>
    <!--<div v-if="sele">-->
    <div style="width: 100%;height: 100%;position: relative;">
      <!---->
      <div style="width: 100%;" class="global_toolbar">
        <el-form :inline="true"  ref="filter" :model="this.filter">

          <el-form-item>
            <el-button v-if="add" type="primary" icon="el-icon-plus"
                       @click="add('other')">
              <span class="toolbar_device_add"></span>
            </el-button>
          </el-form-item>

          <el-form-item>
            <el-input v-model="filter.name" placeholder="" style="width: 14rem">
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-input v-model="filter.email" placeholder="" style="width: 14rem">
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-select v-model="filter.userType" placeholder="" style="width: 14rem">
              <el-option
                v-for="item in userTypeList"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-select v-model="filter.userStatus" placeholder="" style="width: 14rem">
              <el-option
                v-for="item in userStatusList"
                :label="item.label"
                :value="parseInt(item.value)">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="success" icon="el-icon-search" @click.native="query" ></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click.native="resetFilters"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--table-->
      <div style="position: relative">
        <!---->
        <el-table :data="userList"
                  stripe
                  highlight-current-row
                  v-loading="listLoading"
                  :header-row-class-name="'global_table_th'"
                  :row-class-name="'global_table_tr'"
                  :cell-class-name="'global_table_normal'"
                  style="width: 100%;position: absolute;top: 0;left: 0">
          <el-table-column prop="name" label="" min-width="30" align="center">
          </el-table-column>
          <el-table-column prop="email" label="" min-width="80" align="center">
          </el-table-column>
          <el-table-column prop="phone" label="" min-width="80" align="center">
          </el-table-column>
          <el-table-column prop="sex" label="" min-width="60" :formatter="formatUserSex" align="center">
          </el-table-column>
          <el-table-column prop="creditRating" label="" min-width="60" align="center">
          </el-table-column>
          <el-table-column prop="type" label="" min-width="80" :formatter="formatUserType" align="center">
          </el-table-column>
          <el-table-column prop="userStatus" label="" min-width="80" align="center">
            <template scope="scope">
              <el-tag v-if="scope.row.userStatus === 0" type="warning"></el-tag>
              <el-tag v-if="scope.row.userStatus === 2" type="success"></el-tag>
              <el-tag v-if="scope.row.userStatus === 4" type="danger"></el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" width="140" align="center">
            <template scope="scope">
              <el-tooltip  class="item" effect="dark" content="" placement="top">
                <el-button size="mini" type="text" icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)"></el-button>
              </el-tooltip>
              <el-tooltip  class="item" effect="dark" content="" placement="top">
                <el-button size="mini" type="text" icon="el-icon-delete"  @click="handleDel(scope.$index, scope.row)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div :span="24" class="toolbar" style="position: absolute;right: 0;bottom: 10px">
        <el-pagination style="float:right;"
          @current-change="handleCurrentChange"
          :current-page="filter.currentPage"
          :page-size="filter.pageSize"
          layout="total,  prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </div>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="150px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="time">
          <el-date-picker
            :editable="false"
            v-model="editForm.time"
            type="datetime" format="yyyy-MM-dd HH:mm:ss"
            placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="editForm.userId" placeholder="">
            <el-option
              v-for="item in editForm_users_options"
              :key="item.id"
              :label="item.nickName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" placeholder="" v-model="editForm.content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editFormLoading"></el-button>
      </div>
    </el-dialog>
    <!--</div>-->
    <!--<div v-else>-->
    <!--<center><h2>,.</h2></center>-->
    <!--</div>-->
  </section>
</template>

<script>
  import {deleteUserAPI, dropListOneGetApi, userPageFindAPI} from "../api/job";
  import store from "../vuex/store"

  export default {
    name: "usermanagerpage",
    data() {

      return {
        filter: {
          name: "",
          email: "",
          userType: "",
          userStatus: "",
          pageNum:0,
          pageSize:10
        },
        userList: [],
        total:0,
        userTypeList:[],
        userStatusList:[],

        editForm: {
          id: '',
          userId: '',
          time: '',
          content: ''
        },
        editFormRules: {
          userId: [{required: true, message: '', trigger: 'blur'}],
          time: [{required: true, message: '', trigger: 'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}],
        },
        editFormVisible: false,

        loading: false,
        editFormLoading: false,
        listLoading: false,

        maintenance: [],
        total: 0,
        page: 1,
        device_options: [],
        users_options: [],
        editForm_users_options: [],
        lunarData: [],
      }

    },
    methods: {
      resetFilters: function(){
        this.filter = {
          name: "",
          email: "",
          userType: "",
          userStatus: "",
          pageNum:0,
          pageSize:10
        };
      },
      add: function () {

      },
      //
      handleCurrentChange: function (val) {
        this.filter.pageNum = val - 1;
        this.query();
      },
      query: function () {
        console.log("");
        userPageFindAPI(this.filter).then(res => {
          if (res.code === 200){
            this.userList = res.data.content;
            this.total = res.data.totalElements;
          } else {
            if (res.code === 2){
              this.$store.commit('signInDialogVisibleTrue');
            }
            this.$message.error(res.msg);
          }
        }).catch(e => {
          console.log("" + e);
        });

      },
      //todo 
      handleEdit: function () {

      },

      handleDel: function (scop,row) {

        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteUserAPI(row.id).then(res => {
            if (res.code === 200){
              this.query();
              this.$message.success("");
            } else {
              if (res.code === 2){
                this.$store.commit('signInDialogVisibleTrue');
              }
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {
        });

      },

      formatUserType: function (row, column) {
        if (row.type === 0){
          return "";
        } else if (row.type === 1) {
          return "";
        }else {
          return "";
        }
      },

      formatUserSex: function (row, column) {
        if (row.sex === "0"){
          return "";
        } else if (row.sex === "1") {
          return "";
        }else {
          return "";
        }
      }
    },
    mounted(){

      dropListOneGetApi("user_type").then(res => {
        if (res.code === 200){
          this.userTypeList = res.data;
        } else {
          this.$message.error("");
        }
      });

      dropListOneGetApi("user_status").then(res => {
        if (res.code === 200){
          this.userStatusList = res.data;
        } else {
          this.$message.error("");
        }
      });

      this.query();
    }
  }
</script>

<style scoped>

</style>



