<template>
  <section>
    <!--<div v-if="sele">-->
    <div style="width: 100%;height: 100%;position: relative;">
      <!---->
      <div style="width: 100%;" class="global_toolbar">
        <el-form :inline="true" ref="filter" :model="this.filter">

          <el-form-item>
            <el-button type="primary" icon="el-icon-plus"
                       :disabled="userState"
                       @click="addButtonClick">
              <span class="toolbar_device_add"></span>
            </el-button>
          </el-form-item>

          <el-form-item>
            <el-input v-model="filter.jobName" placeholder="" style="width: 14rem">
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-input v-model="filter.contactPerson" placeholder="" style="width: 14rem">
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-cascader
              style="width: 14rem"
              :options="job_type_options"
              placeholder=""
              v-model="filter.jobType">
            </el-cascader>
          </el-form-item>

          <el-form-item>
            <el-select v-model="filter.payMethod" placeholder="" style="width: 14rem">
              <el-option
                v-for="item in job_pay_method_options"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="success" icon="el-icon-search" @click.native="query" :disabled="userState"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click.native="resetFilters" :disabled="userState"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--table-->
      <div style="position: relative">
        <!---->
        <el-table :data="dataList"
                  stripe
                  highlight-current-row
                  :header-row-class-name="'global_table_th'"
                  :row-class-name="'global_table_tr'"
                  :cell-class-name="'global_table_normal'"
                  style="width: 100%;position: absolute;top: 0;left: 0">
          <el-table-column prop="jobName" label="" min-width="80" align="center">
          </el-table-column>
          <el-table-column prop="contactPerson" label="" min-width="50" align="center">
          </el-table-column>
          <el-table-column prop="phone" label="" min-width="50" align="center">
          </el-table-column>
          <el-table-column prop="requireNum" label="" min-width="40" align="center">
          </el-table-column>
          <el-table-column prop="salary" label="" min-width="30" align="center">
          </el-table-column>
          <el-table-column prop="payMethod" label="" min-width="30" :formatter="formatPayMethod" align="center">
          </el-table-column>
          <el-table-column prop="needResume" label="" min-width="40" :formatter="formatNeedResume" align="center">
          </el-table-column>
          <el-table-column prop="gender" label="" min-width="40" :formatter="formatUserSex" align="center">
          </el-table-column>
          <el-table-column label="" width="140" align="center">
            <template scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button size="mini" type="text" icon="el-icon-edit"
                           @click="handleEdit(scope.$index, scope.row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button size="mini" type="text" icon="el-icon-view"
                           @click="handleShow(scope.$index, scope.row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button size="mini" type="text" icon="el-icon-delete"
                           @click="handleDel(scope.$index, scope.row)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div :span="24" class="toolbar" style="position: absolute;right: 0;bottom: 10px">
        <el-pagination style="float:right;"
                       :disabled="userState"
                       @current-change="handleCurrentChange"
                       :current-page="filter.currentPage"
                       :page-size="filter.pageSize"
                       layout="total,  prev, pager, next, jumper"
                       :total="total">
        </el-pagination>
      </div>
    </div>

    <!---->
    <el-dialog
      :title="title"
      width="70%"
      center
      :close-on-press-escape="true"
      @close="editDialogClose"
      :visible.sync="editFormVisible"
      :close-on-click-modal="false">

      <el-form :model="editForm"
               label-width="7rem"
               :rules="editFormRules"
               ref="editForm">

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="jobName">
              <el-input v-model="editForm.jobName" autocomplete="off" style="width: 25rem"
                        placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="jobType">
              <el-cascader
                style="width: 25rem"
                :options="job_type_options"
                v-model="editForm.jobType">
              </el-cascader>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="contactPerson">
              <el-input v-model="editForm.contactPerson" autocomplete="off" style="width: 25rem"
                        placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="phone">
              <el-input v-model="editForm.phone" autocomplete="off" style="width: 25rem"
                        placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="salary">
              <el-input-number v-model="editForm.salary" :precision="2" :min="1" :max="1000000" :step="10"
                               label="" style="width: 25rem"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="requireNum">
              <el-input-number v-model="editForm.requireNum" :precision="0" :min="1" :max="1000" :step="1"
                               label="" style="width: 25rem"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>

          <el-col :span="12">
            <el-form-item label="" prop="type">
              <el-select v-model="editForm.type" placeholder="" style="width: 25rem">
                <el-option
                  v-for="item in editForm_type_options"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item label="" v-show="editForm.type === 0">
              <el-cascader
                style="width: 25rem"
                :options="region_options"
                v-model="editForm.region">
              </el-cascader>
            </el-form-item>
          </el-col>

        </el-row>

        <el-row v-show="editForm.type === 0">
          <el-col :span="24">
            <el-form-item label="">
              <el-input v-model="editForm.workLocation" autocomplete="off" style="width: 65.875rem"
                        placeholder=""></el-input>
            </el-form-item>

          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="payMethod">
              <el-select v-model="editForm.payMethod" placeholder="" style="width: 25rem">
                <el-option
                  v-for="item in job_pay_method_options"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="salaryTreatment">
              <el-select v-model="editForm.salaryTreatment"
                         multiple
                         placeholder="" style="width: 25rem">
                <el-option
                  v-for="item in job_salary_treatment_options"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="gender">
              <el-select v-model="editForm.gender" placeholder="" style="width: 25rem">
                <el-option
                  v-for="item in editForm_gender_options"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="needResume">
              <el-radio-group v-model="editForm.needResume" size="normal" style="width: 25rem">
                <el-radio-button label="false"></el-radio-button>
                <el-radio-button label="true"></el-radio-button>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="enableStatus">
              <el-select v-model="editForm.enableStatus" placeholder="" style="width: 25rem">
                <el-option
                  v-for="item in editForm_enableStatus_options"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
          </el-col>
        </el-row>

        <el-row>
          <el-form-item label="" prop="jobDetails">
            <vue-editor v-model="editForm.jobDetails"></vue-editor>
          </el-form-item>
        </el-row>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="add"></el-button>
      </div>
    </el-dialog>
    <!--</div>-->
    <!--<div v-else>-->
    <!--<center><h2>,.</h2></center>-->
    <!--</div>-->
  </section>
</template>

<script>

  import {
    addJobAPI,
    companyJobPageAPI, deleteJobAPI,
    deleteUserAPI,
    dropListOneGetApi,
    getJobTypeAPI,
    userPageFindAPI
  } from "../api/job";
  import store from "../vuex/store"
  import * as util from "../common/utils/util"
  import {VueEditor} from 'vue2-editor'
  import {getPositionOption} from "../common/js/position";

  export default {
    name: "companyJobManagerPage",
    components: {
      VueEditor
    },
    data() {

      return {
        mainName: "",
        //options --------------------------
        editForm_gender_options: [{
          value: -1,
          label: ''
        }, {
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }],
        editForm_type_options: [{
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }],
        job_salary_treatment_options: [],
        job_pay_method_options: [],
        job_type_options: [],
        region_options: [],
        editForm_enableStatus_options: [],
        userTypeList: [],
        userStatusList: [],
        //--------
        filter: {
          jobName: "",
          contactPerson: "",
          jobType: [],
          payMethod: "",
          pageNum: 0,
          pageSize: 10
        },
        editForm: {
          jobName: '',
          jobType: [],
          contactPerson: '',
          phone: '',
          salary: '',
          requireNum: '',
          type: '',
          region: [],
          workLocation: '',
          payMethod: '0',
          salaryTreatment: [],
          gender: -1,
          needResume: false,
          jobDetails: '',

          popularScore: 0,
          enableStatus: "3"
        },
        editFormRules: {
          jobName: [{required: true, message: '', trigger: 'blur'}],
          jobType: [{required: true, message: '', trigger: 'blur'}],
          contactPerson: [{required: true, message: '', trigger: 'blur'}],
          phone: [{required: true, message: '', trigger: 'blur'}],
          salary: [{required: true, message: '', trigger: 'blur'}],
          requireNum: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          payMethod: [{required: true, message: '', trigger: 'blur'}],
          gender: [{required: true, message: '', trigger: 'blur'}],
          needResume: [{required: true, message: '', trigger: 'blur'}],
          jobDetails: [{required: true, message: '', trigger: 'blur'}],
          enableStatus: [{required: true, message: '', trigger: 'blur'}]
        },
        //----------
        dataList: [],
        total: 0,
        editFormVisible: false,
        total: 0,
        title: "",
      }

    },
    computed: {
      userState: {
        get: function () {
          let str = localStorage.getItem("user");
          if (util.isEmpty(str)) {
            console.log("user ")
            return true;
          } else {
            let user = JSON.parse(str);
            if (user.userStatus === 4) {
              console.log("");
              return true;
            } else {
              console.log("");
              return false;
            }
          }
        },
        set: function () {

        }
      }
    },
    methods: {
      //todo 
      handleShow: function (index, row) {

      },
      //------------------------------
      resetFilters: function () {
        this.filter = {
          jobName: "",
          contactPerson: "",
          jobType: [],
          payMethod: "",
          pageNum: 0,
          pageSize: 10
        };
        this.query();
      },

      resetEditForm: function () {
        this.$refs["editForm"].resetFields();
        this.editForm = {
          jobName: '',
          jobType: [],
          contactPerson: JSON.parse(localStorage.getItem("user")).name,
          phone: JSON.parse(localStorage.getItem("user")).phone,
          salary: '',
          requireNum: '',
          type: '',
          region: [],
          workLocation: '',
          payMethod: '0',
          salaryTreatment: [],
          gender: -1,
          needResume: false,
          jobDetails: '',

          popularScore: 0,
          enableStatus: "3"
        };
      },

      //-----------------------

      handleEdit: function (index, row) {
        this.editForm = JSON.parse(JSON.stringify(row));
        this.editFormVisible = true;
        this.title = "" + this.mainName;
      },

      editDialogClose: function () {
        this.resetEditForm();
      },

      //
      handleCurrentChange: function (val) {
        this.filter.pageNum = val - 1;
        this.query();
      },

      //  
      add: function () {
        this.$refs["editForm"].validate((valid) => {
          if (valid) {
            addJobAPI(this.editForm).then(res => {
              if (res.code === 200) {
                if (this.title === "" + this.mainName) {
                  this.$message.success(""+ this
                    .mainName + "");
                } else {
                  this.$message.success(""+this.mainName+"");
                }
                this.editFormVisible = false;
                this.query();
              } else if (res.code === 2) {
                this.$store.commit('signInDialogVisibleTrue');
              } else {
                this.$message.error(res.msg)
              }
            })
          } else {
            return false;
          }
        });
      },

      query: function () {
        console.log("");
        companyJobPageAPI(this.filter).then(res => {
          if (res.code === 200) {
            this.dataList = res.data.content;
            this.total = res.data.totalElements;
          } else if (res.code === 2) {
            this.$store.commit('signInDialogVisibleTrue');
          } else {
            this.$message.error(res.msg)
          }
        }).catch(e => {
          console.log("" + e);
        });

      },

      //
      addButtonClick: function () {
        this.editFormVisible = true;
        this.title = '' + this.mainName;
      },
      //
      handleDel: function (scop, row) {

        this.$confirm(''+this.mainName+', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteJobAPI(row.id).then(res => {
            if (res.code === 200) {
              this.query();
              this.$message.success("");
            } else {
              if (res.code === 2) {
                this.$store.commit('signInDialogVisibleTrue');
              }
              this.$message.error(res.msg);
            }
          })
        }).catch(() => {
        });

      },

      //format--------------------------------------
      formatPayMethod: function (row, column) {
        switch (row.payMethod) {
          case "0" :
            return "";
          case "1" :
            return "";
          case "2" :
            return "";
          case "3" :
            return "";
          case "4" :
            return "";
          case "5" :
            return "";
          default:
            return "";
        }

      },

      formatNeedResume: function (row, column) {
        if (row.needResume === true) {
          return "";
        } else if (row.needResume === false) {
          return "";
        } else {
          return "";
        }
      },

      formatUserSex: function (row, column) {
        if (row.gender === 0) {
          return "";
        } else if (row.sex === 1) {
          return "";
        } else {
          return "";
        }
      }
    },
    mounted() {

      dropListOneGetApi("user_type").then(res => {
        if (res.code === 200) {
          this.userTypeList = res.data;
        } else {
          console.error("");
        }
      });

      dropListOneGetApi("user_status").then(res => {
        if (res.code === 200) {
          this.userStatusList = res.data;
        } else {
          console.error("");
        }
      });

      dropListOneGetApi("job_pay_method").then(res => {
        if (res.code === 200) {
          this.job_pay_method_options = res.data;
        } else {
          console.error("");
        }
      });

      dropListOneGetApi("job_salary_treatment").then(res => {
        if (res.code === 200) {
          this.job_salary_treatment_options = res.data;
        } else {
          console.error("");
        }
      });

      dropListOneGetApi("editForm_enableStatus_options").then(res => {
        if (res.code === 200) {
          this.editForm_enableStatus_options = res.data;
        } else {
          console.error("");
        }
      });

      getJobTypeAPI().then(res => {
        if (res.code === 200) {
          this.job_type_options = res.data;
        } else {
          console.error("");
        }

      });

      this.editForm.contactPerson = JSON.parse(localStorage.getItem("user")).name;
      this.editForm.phone = JSON.parse(localStorage.getItem("user")).phone;
      this.region_options = getPositionOption();
      this.query();
    }
  }
</script>

<style scoped>

</style>



