import home from '../components/home.vue'
import indexPage from '../components/indexPage.vue'
import dataAnalysePage from '../components/dataAnalysePage.vue'
import companyJobManagerPage from '../components/companyJobManagerPage.vue'
import adminJobManagerPage from '../components/adminJobManagerPage.vue'
import jobPage from '../components/jobPage.vue'
import jobTypeManagerPage from '../components/jobTypeManagerPage.vue'
import messagePage from '../components/messagePage.vue'
import userManagerPage from '../components/userManagerPage.vue'
import personCenter from '../components/personCenter.vue'


let routes = [
  {
    path: '/',
    component: home,
    redirect:'/indexPage',
    name: '',
    iconCls: 'el-icon-message',//class
    children: [
      {path: '/indexPage', component: indexPage, name: '', hidden: true},
      {path: '/jobPage', component: jobPage, name: '', hidden: true},
      {path: '/userManagerPage', component: userManagerPage, name: '', hidden: true},
      {path: '/jobTypeManagerPage', component: jobTypeManagerPage, name: '', hidden: true},
      {path: '/companyJobManagerPage', component: companyJobManagerPage, name: '', hidden: true},
      {path: '/adminJobManagerPage', component: adminJobManagerPage, name: '', hidden: true},
      {path: '/dataAnalysePage', component: dataAnalysePage, name: '', hidden: true},
      {path: '/messagePage', component: messagePage, name: '', hidden: true},
      {path: '/personCenter', component: personCenter, name: '', hidden: true}
    ]
  },
];

export default routes;



